import userService from '../services/users'
import linkService from '../services/links'

const userReducer = (store = [], action) => {
  switch(action.type) {
    case 'INIT_USERS':
      console.log('Initiate users userReducer')
      return action.data
    //Uusi user, tsekkaa onko t ihan vituillaan
    case 'NEW_USER':
      console.log('Uuden kyttjn lisys userReducer')
      return [...store, action.data]
    case 'ADD_LINK_FOR_USER':
      console.log('ADD_LINK_FOR_USER userReducer')
      let old = []
      let modified = []
      store.forEach(u => {
        if (u.id === action.userId) {
          modified.push(u)
        } else {
          old.push(u)
        }
      })
      /*Nyt siis pelkstn kyttjn favouritelinkkien tila on pivitetty*/
      return [...old, {...modified[0], links: [...modified[0].links, action.data]}]
    case 'ADD_PLAYLIST_FOR_USER':
      console.log('ADD_PLAYLIST_FOR_USER userReducer')
      let vanhat = []
      let muokatut = []
      store.forEach(u => {
        if (u.id === action.userId) {
          muokatut.push(u)
        } else {
          vanhat.push(u)
        }
      })
      return [...vanhat, {...muokatut[0], playlists: [...muokatut[0].playlists, action.data]}]
    default:
      console.log('default in userReducer')
      return store
  }
}

export const usersInitialization = () => {
  console.log('usersInitialization in userReducer')
  const formatUser = (user) => {
    return {
      id: user.id,
      username: user.username,
      name: user.name,
      links: user.links,
      playlists: user.playlists
    }
  }

  return async (dispatch) => {
    const users = await userService.getAll()
    let formattedUsers = []
    users.forEach(u => {
      formattedUsers.push(formatUser(u))
    })
    dispatch({
      type: 'INIT_USERS',
      data: formattedUsers
    })
  }
}

export const addNewUser = (userObject) => {
  console.log('kutsuttu addNewUseria userReducerissa')
  const formatUser = (user) => {
    return {
      id: user.id,
      username: user.username,
      name: user.name,
      links: user.links,
      playlists: user.playlists

      //Favorite linkit ?? (ja muut kentt??)
    }
  }
  return async (dispatch) => {
    try {
      const user = await userService.addUser(userObject)
      const formattedUser = formatUser(user)
      dispatch({
        type: 'NEW_USER',
        data: formattedUser
      })
    } catch (e) {
      return "error"
    }
  }
}

/*T saattais olla fiksumpaa sijoittaa linkReduceriin*/
export const addFavouriteForUser = (linkObject, userId) => {
  console.log('addFavouriteForUser userService')
  /*YTSearchResultissa on jo tarkistettu ettei kyttjll ole jo kyseist
  linkki suosikeissa.*/

  return async (dispatch) => {
    const link = await linkService.createAndAddLinkToUserFavourites(linkObject)
    /*Tmn jlkeen tietokantaan on listty kyttjn links kenttn
    tm uusi linkki. Ja linkkitietokantaan on listty kyseinen linkki, jos
    se ei ollut siell aiemmin.*/
    dispatch({
      type: 'ADD_LINK_FOR_USER',
      data: link,
      userId: userId
    })
  }
}

export const addPlaylistForUser = (playlistObject, userId) => {
  console.log('addPlaylistForUser userReducer')
  /*Tarkistetaan taas jo ennen tnne psy onko samannimnen soittolista
  jo olemassa!!*/
  return async (dispatch) => {
    try {
      const playlist = await linkService.createPlaylist(playlistObject)
      dispatch({
        type: 'ADD_PLAYLIST_FOR_USER',
        data: playlist,
        userId: userId
      })
    } catch (exception) {
      /*Joku random error.*/
      console.log('error')
    }

  }
}

export default userReducer



